    background: transparent;
  }
  *::-webkit-scrollbar-thumb {
    background-image: linear-gradient(to bottom, hsl(var(--primary)), hsl(var(--accent)));
    border-radius: 999px;
    border: 2px solid rgba(5, 5, 16, 0.95);
  }
  body {
    background-color: hsl(var(--background));
    background-image: radial-gradient(circle at 0% -20%, rgba(255, 255, 255, 0.14), transparent 55%),
      radial-gradient(circle at 100% -20%, rgba(255, 255, 255, 0.10), transparent 55%),
      
      radial-gradient(circle at 50% 130%, rgba(255, 255, 255, 0.10), transparent 70%),
      
      linear-gradient(to bottom, #050510 0%, #040413 40%, #03030f 100%);
    color: hsl(var(--foreground));
    font-feature-settings: "rlig" 1, "calt" 1;
  }
  h1, h2, h3 {
    font-weight: 700;
    letter-spacing: -0.04em;
  }
  h4, h5, h6 {
    font-weight: 600;
    letter-spacing: -0.02em;
  }
  p, li {
    letter-spacing: 0.01em;
  }
